'use client';

import { useEffect } from 'react';
import Link from 'next/link';
import { Button } from '../loan/components/ui/button';

export default function LoanError({
  error,
  reset,
}: {
  error: Error & { digest?: string };
  reset: () => void;
}) {
  useEffect(() => {
    console.error('Loan page error:', error);
  }, [error]);

  return (
    <div className="flex min-h-screen flex-col items-center justify-center bg-white">
      <div className="mx-auto max-w-md text-center px-4">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">Failed to load loan page</h1>
        <p className="text-gray-600 mb-8">
          {error.message || 'An error occurred while loading the loan application'}
        </p>
        <div className="flex gap-4 justify-center">
          <Button onClick={reset} variant="default">
            Try again
          </Button>
          <Link href="/">
            <Button variant="outline">
              Back to Home
            </Button>
          </Link>
        </div>
      </div>
    </div>
  );
}

